import { Basic } from 'summer';
import { CentroCusto } from '../ger/centroCusto';
import { Fpas } from './fpas';
import { Gps } from './gps';
import { LotacaoTributaria } from './lotacaoTributaria';
import { TomadorServicoEmail } from './tomadorServicoEmail';
import { TomadorServicoEndereco } from './tomadorServicoEndereco';
import { TomadorServicoTelefone } from './tomadorServicoTelefone';

export interface TomadorServico extends Basic {
  aliquotaFap?: number;
  aliquotaRat?: number;
  caepf?: string;
  cei?: string;
  centroCusto?: CentroCusto;
  cnaef?: string;
  cnaefDesmembramento?: string;
  cnpj?: string;
  codigo?: number;
  codigoDeducaoTerceiros?: string;
  codigoFpas?: Fpas;
  codigoGps?: Gps;
  codigoGpsTerceiros?: Gps;
  /** este campo utiliza o enum CodigoRecolhimentoEnum */
  codigoRecolhimentoSefip?: string;
  contribuicaoPatronalSubstituida?: boolean;
  /** este campo utiliza o enum TipoControlePontoEnum */
  controlePonto?: string;
  emails?: TomadorServicoEmail[];
  enderecos?: TomadorServicoEndereco[];
  /** este campo utiliza o enum IndicativoContratacaoAprendizEnum */
  indicativoContratacaoAprendiz?: string;
  /** este campo utiliza o enum IndicativoContratacaoPCDEnum */
  indicativoContratacaoPCD?: string;
  isCnpjInscricaoContratante?: boolean;
  isCnpjInscricaoProprietario?: boolean;
  lotacaoTributaria?: LotacaoTributaria;
  nome?: string;
  nrInscricaoProprietario?: string;
  nrInscricaoContratante?: string;
  percentualDesoneracaoGps?: number;
  /** este campo utiliza o enum TipoCAEPFEnum */
  tipoCaepf?: string;
  telefones?: TomadorServicoTelefone[];
}

export const CLASS_NAME = 'br.com.projetusinformatica.calima.mfp.model.dto.tomadorservico';
