import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/tomadorServico';

const TomadorServicoIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Código',
          },
          {
            dataField: 'nome',
            text: 'Nome',
          },
          {
            dataField: 'cnpj',
            text: 'CNPJ',
          },
          {
            dataField: 'cei',
            text: 'CEI',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'TomadorServicoSearchView',
      }}
    />
  );
};

export default TomadorServicoIS;
