import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/ger/centroCusto';

const CentroCustoIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Código',
          },
          {
            dataField: 'descricao',
            text: 'Descrição',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'CentroCustoSearchView',
      }}
      label={props.label ? props.label : 'Centro de Custo'}
      placeholder={props.placeholder ? props.placeholder : 'Informe o Centro de Custo'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default CentroCustoIS;
